import styles from './selectCountries.module.scss'
import {useMemo, useState} from "react";
import closeIco from '../../shared/images/close_dark.svg'
import {allTimeZone} from "../../shared/constants/countries";


export const SelectCountries = ({country, setCountry}) => {
    const [isActive, setActive] = useState(false)
    const [countries, setCountries] = useState([])

    useMemo(() => {

        const newData = allTimeZone.map((i) => {
            if (
                (i.country_code === 'RU') ||
                (i.country_code === 'KZ') ||
                (i.country_code === 'BY')
            ) {
                setCountries(prev => [i, ...prev])
            } else {
                setCountries(prev => [...prev, i])
            }
        })
    }, [])

    const sortHandler = (e) => {
        e.stopPropagation()
        e.preventDefault()
        const value = e.target.value
        console.log(value)
        if (value === '') {
            return setCountries(allTimeZone)
        }

        const newData = allTimeZone.filter((i) => {
            if (i.country.toLowerCase().includes(value.toLowerCase())) {
                if (i.country.toLowerCase() === value.toLowerCase()) {
                    console.log(i.country.toLowerCase(), value.toLowerCase())
                    return i
                }
                return i
            }

            if (value.includes('+')) {
                if (i.phone_code === value) {
                    return i
                }
                if (i.phone_code.includes(value)) {
                    return i
                }
            }
            if (Number(value)) {
                if (!value.includes('+')) {
                    let numberCountry = i.phone_code
                    console.log(Number(value))
                    if (i.phone_code.includes('+')) numberCountry.replace('+', '')
                    if (i.phone_code.includes('-')) numberCountry.replace('-', '')

                    if (numberCountry.includes(Number(value))) {
                        return i
                    }
                }

            }
        })

        if (newData.length > 0) {
            setCountries(newData)
        }
        console.log(newData)
    }

    const handleClick = (val) => {
        setCountry(val)
        setActive(false)
    }

    return (
        <div
            onClick={() => setActive(prev => !prev)}
            className={styles.body}>
            <img width={33} src={`https://flagcdn.com/w40/${country.country_code.toLowerCase()}.png`} alt=""/>
            <button>
                <svg width="12" height="8" viewBox="0 0 12 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M6.75 6C6.75 5.58579 6.41421 5.25 6 5.25C5.58579 5.25 5.25 5.58579 5.25 6H6.75ZM5.46967 7.03033C5.76256 7.32322 6.23744 7.32322 6.53033 7.03033L11.3033 2.25736C11.5962 1.96447 11.5962 1.48959 11.3033 1.1967C11.0104 0.903806 10.5355 0.903806 10.2426 1.1967L6 5.43934L1.75736 1.1967C1.46447 0.903806 0.989593 0.903806 0.696699 1.1967C0.403806 1.48959 0.403806 1.96447 0.696699 2.25736L5.46967 7.03033ZM5.25 6V6.5H6.75V6H5.25Z" fill="#C2C2C2"/>
                </svg>
            </button>

            <p>
                {country.phone_code}
            </p>

            <div onChange={sortHandler} onClick={(e) => e.stopPropagation()} className={`${styles.list} ${!isActive ? `${styles.hide}` : ''}`}>
                <div className={'mobile-container'}>
                    <div className={styles.searchCountry}>
                        <input type="text" placeholder={'Введите название страны'}/>
                        <button onClick={() => setActive(false)}>
                            <img src={closeIco} alt=""/>
                        </button>
                    </div>
                    <div className={styles.wrapper}>
                        {
                            countries.map((i) => (
                                <div onClick={() => handleClick(i)} className={styles.listItem}>
                                    <img width={33}  key={i.country_code} src={`https://flagcdn.com/w40/${i.country_code.toLowerCase()}.png`} alt=""/>
                                    <p>{i.country} ({i.phone_code})</p>
                                </div>
                            ))
                        }
                    </div>

                </div>

            </div>
        </div>
    )
}